import "./styles.css";
import React, { useState } from "react";

export default function App() {
  const [step, setStep] = useState(0);

  const request = {
    steps: [
      {
        type: "auth",
        content: [
          {
            key: "id",
            value: "1234"
          },
          {
            key: "birthday",
            value: "0607"
          }
        ]
      },
      {
        type: "summary",
        content: [{}]
      },
      {
        type: "review",
        content: [
          {
            name: ""
          }
        ]
      },
      {
        type: "upload",
        content: [
          {
            name: ""
          },
          { name: "" }
        ]
      },
      {
        type: "sign",
        content: [
          {
            role: "",
            name: ""
          }
        ]
      }
    ]
  };

  return (
    <div className="App">
      <h1>{request.steps[step].type}</h1>
      {request.steps[step].type === "auth" && (
        <Auth auth={request.steps[step].content}></Auth>
      )}

      <button onClick={() => setStep(step + 1)}>Next</button>
    </div>
  );
}

const Auth = ({ auth }) => {
  const input = auth.map(({ key, value }) => {
    return (
      <div>
        {key}:
        <input key={key} />
      </div>
    );
  });
  return <div>{input}</div>;
};
